const INITIAL_STATE = {
    nodes: [],
    currBitnode: null
}
export function nodeReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case 'SET_RECIPES':
            return {
                ...state,
                nodes: action.nodes
            }
        case 'SET_RECIPE':
            return {
                ...state,
                currBitnode: action.node
            }
        case 'REMOVE_RECIPE':
            return {
                ...state,
                nodes: state.nodes.filter(node => node._id !== action.nodeId)
            }
        case 'ADD_RECIPE':
            return {
                ...state,
                nodes: [...state.nodes, action.node],
            }
        case 'UPDATE_RECIPE':
            return {
                ...state,
                nodes: state.nodes.map(node => node._id === action.node._id ? action.node : node)
            }
        default:
            return state
    }
}