
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom'
import { BitnodeFilter } from '../../cmps/BitnodeFilter/BitnodeFilter';
import { BitnodeList } from '../../cmps/BitnodeList/BitnodeList';
import nodeService from '../../services/nodeService';
import { loadBitnodes } from '../../store/actions/nodeActions';
import Select from 'react-select'
// import ReactSelect from '../../cmps/react-select'
import './BitnodeApp.scss'
import plus from '../../assets/icons/plus.png'

export function BitnodeApp(props) {
    const [filterBy, setFilterBy] = useState(null)
    var nodes = useSelector(state => state.nodeReducer.nodes)
    console.log(props.location.search);
    if (props.location.search) {
        var res = props.location.search.substring(1)
        var resArr = res.split('=')
        const field = resArr[0]
        const value = resArr[1]
        if (field === 'term') {
            const term = value.toLocaleLowerCase()
            nodes = nodes.filter(node => {
                return node.title.toLocaleLowerCase().includes(term) ||
                    node.description.toLocaleLowerCase().includes(term)
            })
        } else {
            nodes = nodes.filter(node => {
                console.log(node[field]);
                if (field === 'author') return node[field].name === value.replace('%20', ' ')
                return node[field].includes(value)
            })
        }
    }


    const dispatch = useDispatch()
    useEffect(() => {
        console.log('effect');
        dispatch(loadBitnodes(filterBy))
    }, [filterBy])

    const onSetFilter = (filterBy) => {
        setFilterBy(filterBy)
    }

    const onSelectBitnode = (selectedBitnodeId) => {
        props.history.push(`/node/${selectedBitnodeId}`)
    }

    const options = [
        { value: 'Quick', label: 'Quick' },
        { value: 'Vegetarian', label: 'Vegetarian' },
        { value: 'Dinner', label: 'Dinner' },
        { value: 'Show All', label: 'Show All' }
    ]

    return (
        <div className="node-app flex column align-center">
            <Link className="add-btn btn" to="/node/edit"><img src={plus} /></Link>
            <section className="filter text-center flex column auto-center">
                <h1>Bitnodes</h1>
                <BitnodeFilter onSetFilter={onSetFilter} />
                <section className="nav-btns flex auto-center">
                    <button onClick={() => props.history.push(`/node?tags=Quick`)}>Quick</button>
                    <button onClick={() => props.history.push(`/node?tags=Vegetarian`)}>Vegeterian</button>
                    <button onClick={() => props.history.push(`/node?tags=Dinner`)}>Dinner</button>
                    <button onClick={() => props.history.push(`/node`)}>Show All</button>
                </section>
                <Select className="nav-btns-mobile" onChange={(ev) => {
                    if(ev.value === 'Show All') props.history.push(`/node`)
                    else props.history.push(`/node?tags=${ev.value}`)
                }} options={options} />
            </section>
            {nodes && <BitnodeList onSelectBitnode={onSelectBitnode} nodes={nodes} />}
        </div>
    )
}

