import { BitnodePreview } from '../BitnodePreview/BitnodePreview';
import './BitnodeList.scss'
export function BitnodeList({ nodes, onSelectBitnode }) {
  console.log(nodes);
  return nodes.length ? (
    <ul className="node-list clean-list flex wrap auto-center ">
      {
        nodes.map(node => <BitnodePreview onSelectBitnode={onSelectBitnode} node={node} key={node._id} />)
      }
    </ul>
  ) : <div className="node-list-default">There is no nodes</div>
}