
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom'
import { BitnodeList } from '../../cmps/BitnodeList/BitnodeList';
import { loadBitnodes } from '../../store/actions/nodeActions';

import nodeService from '../../services/nodeService';
import './HomePage.scss'
import hero from '../../assets/imgs/hero.jpg'
import search from '../../assets/icons/search.png'
export function HomePage(props) {
    const [term, setTerm] = useState('')
    const nodes = useSelector(state => state.nodeReducer.nodes)
    const dispatch = useDispatch()
    useEffect(() => {
        console.log('effect');
        dispatch(loadBitnodes())
    }, [])

    var nodesToShow = []
    nodesToShow = nodes.slice(0, 3)
    function nodesToShowByTag(tag) {
        return nodes.filter(node => node.tags.includes(tag)).splice(0, 3)
    }
    const onSelectBitnode = (selectedBitnodeId) => {
        props.history.push(`/node/${selectedBitnodeId}`)
    }
    const handleChange = ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        console.log(value);
        setTerm(value)
    }
    const searchBitnodes = (ev) => {
        ev.preventDefault()
        props.history.push(`/node?term=${term}`)
    }
    return (
        <section className="home-page flex column align-center full">
            <div className="img-container mb15">
                <section className="contant flex column auto-center text-center">
                    <section className="hero-text flex column auto-center text-center mb15">
                        <h2>Welcome To Delecious</h2>
                        <h4>Let's create the right meal for you and those around you</h4>
                    </section>
                    <form className="flex" onSubmit={searchBitnodes}>
                        <input type="text" placeholder="Find a node" name="term" value={term} onChange={handleChange} />
                        <img onClick={searchBitnodes} src={search} alt=""  />
                    </form>
                    <section className="nav-btns flex wrap auto-center">
                        <button onClick={() => props.history.push(`/node?tags=Quick`)}>Quick</button>
                        <button onClick={() => props.history.push(`/node?tags=Vegetarian`)}>Vegeterian</button>
                        <button onClick={() => props.history.push(`/node?tags=Dinner`)}>Dinner</button>
                        <button onClick={() => props.history.push(`/node`)}>Show All</button>
                    </section>
                </section>
            </div>
            <section className="previews">
                <section className="nav-contant flex align-center space-between">
                    <h3>Dinner</h3>
                    <h3><Link to="/node">Show All</Link></h3>
                </section>
                <div className="contant flex column">
                    {nodesToShowByTag('Dinner') && <BitnodeList onSelectBitnode={onSelectBitnode} nodes={nodesToShowByTag('Dinner')} />}
                </div>
            </section>
            <section className="previews">
                <section className="nav-contant flex align-center space-between">
                    <h3>Vegetarian</h3>
                    <h3><Link to="/node">Show All</Link></h3>
                </section>
                <div className="contant flex column">
                    {nodesToShowByTag('Vegetarian') && <BitnodeList onSelectBitnode={onSelectBitnode} nodes={nodesToShowByTag('Vegetarian')} />}
                </div>
            </section>
        </section>
    )
}

