import React from 'react'
import './BitnodePreview.scss'
export function BitnodePreview({ node, onSelectBitnode }) {

  function selectBitnode(ev) {
    ev.stopPropagation()
    onSelectBitnode(node._id)
  }
  return node && (
    <li className="node-preview flex column align-center hover-pointer" onClick={selectBitnode}>
      <div className="node-img-container">
        <img src={node.imgUrl} alt="" />
      </div>
      <div className="contant flex column">
        <div className="title-container">
          <h3>{node.title}</h3>
        </div>
        Author:<h3>{node.author.name}</h3>
      </div>
    </li>
  )
}
