import nodeService from '../../services/nodeService'

// Action Dispatcher
export function loadBitnodes(filterBy) {
    return async dispatch => {
        const nodes = await nodeService.query(filterBy)
        console.log(nodes);
        dispatch({ type: 'SET_RECIPES', nodes })
    }
}

export function getById(nodeId) {
    return async dispatch => {
        const node = await nodeService.getById(nodeId)
        dispatch({ type: 'SET_RECIPE', node })
    }
}
export function removeBitnode(nodeId) {
    return async dispatch => {
        try {
            await nodeService.remove(nodeId)
            dispatch({ type: 'REMOVE_RECIPE', nodeId })
        } catch (err) {
            console.log('ERROR!');
        }
    }
}


export function addBitnode(node) {
    return async dispatch => {
        try {
            const savedBitnode = await nodeService.save(node)
            console.log('savedBitnode', savedBitnode);
            dispatch({ type: 'ADD_RECIPE', node: savedBitnode })
        } catch (err) {
            console.log('ERROR!');
        }
    }
}
export function updateBitnode(node) {
    return async dispatch => {
        try {
            const savedBitnode = await nodeService.save(node)
            dispatch({ type: 'UPDATE_RECIPE', node: savedBitnode })
        } catch (err) {
            console.log('ERROR!');
        }
    }
}